.slider-container {
  max-width: 600px;
  margin: 20px auto;
  margin-top: 120px;
  background: #ffffff;
  border: 1px solid #ddd;
  border-radius: 8px;
 
}

.slider-heading {
  text-align: center;
  margin-bottom: 20px;
}

.slider-form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.slider-form input[type="text"],
.slider-form textarea,
.slider-form input[type="file"] {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 80%;
  margin: auto;
}

.slider-form textarea {
  resize: vertical;
  min-height: 80px;
}

.slider-form button {
  padding: 15px;
  background-color: #007bff;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 82%;
  margin: auto;
  margin-bottom: 10px;
}

.slider-form button:hover {
  background-color: #0056b3;
}

.slider-preview {
  margin-top: 30px;
  padding: 10px;
}

.preview-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

.preview-card {
  flex: 1 1 100%;
  border: 1px solid #ccc;
  padding: 12px;
  border-radius: 6px;
  background-color: #f9f9f9;
  margin-bottom: 10px;
}

.preview-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 6px;
  margin-bottom: 10px;
}

/* Media Query */
@media screen and (min-width: 600px) {
  .preview-card {
    flex: 1 1 calc(50% - 16px);
  }
}

@media screen and (min-width: 900px) {
  .preview-card {
    flex: 1 1 calc(33.33% - 16px);
  }

  .slider-container {
    max-width: 800px;
  }
}
