.terms-container {
  background-size: cover;
  max-width: 800px;
  height: auto;
  margin: 0 auto;
  padding: 20px 20px;
  font-family: Arial, sans-serif;
  color: #333;
  line-height: 1.6;
}

.terms-container h1 {
  font-size: 32px;
  margin-bottom: 20px;
  text-align: center;
}

.terms-container h2 {
  font-size: 24px;
  margin-top: 30px;
  text-align: left;
}

.terms-container ul {
  list-style-type: disc;
  padding-left: 20px;
}

.terms-container a {
  color: #007bff;
  text-decoration: none;
}

.terms-container a:hover {
  text-decoration: underline;
}

/* Media Query for screen width 1024px and below */
@media (max-width: 1024px) {
  .terms-container {
    padding: 20px 30px;
  }

  .terms-container h1 {
    font-size: 28px;
  }

  .terms-container h2 {
    font-size: 22px;
  }
}

/* Media Query for screen width 768px and below */
@media (max-width: 768px) {
  .terms-container {
    padding: 15px 20px;
  }

  .terms-container h1 {
    font-size: 24px;
  }

  .terms-container h2 {
    font-size: 20px;
  }
}

/* Media Query for screen width 480px and below */
@media (max-width: 480px) {
  .terms-container {
    padding: 10px 15px;
  }

  .terms-container h1 {
    font-size: 20px;
  }

  .terms-container h2 {
    font-size: 18px;
  }

  .terms-container ul {
    padding-left: 15px;
  }
}
