/* ContactForm.css */

.hero {
  width: 100%;
  height: 350px;
  background-image: url("../Image/about.jpg");
  background-size: cover;
  background-position: center;
  padding-top: 150px;
  text-align: center;
}

.contact-p {
  width: 800px;
  margin: auto;
  font-size: 16px;
  color: wheat;
}

.contact-container {
  max-width: 1200px;
  margin: auto;
  display: flex;
  text-align: left;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 20px;
  background: #f5f8fc;
}

.contact-heading {
  text-align: left;
}

.contact-info {
  width: 50%;
}

.contact-form {
  width: 45%;
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.contact-details {
  margin-top: 20px;
}

.info-item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  gap: 40px;
}

.contact-icon {
  font-size: 24px;
  color: #ff6600;
}

.form-group {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  flex-wrap: wrap;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.textarea {
  width: 100%;
  height: 100px;
}

.cont-btn {
  width: 100%;
  padding: 10px;
  background: #f25c24;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

/* Tablet Screen */
@media (max-width: 768px) {
  .contact-container {
    flex-direction: column;
    align-items: center;
  }

  .contact-info,
  .contact-form {
    width: 90%;
  }

  .form-group {
    flex-direction: column;
  }

  .form-group input,
  .form-group textarea {
    width: 90%;
    margin:auto;
    margin-bottom: 10px;
  }

  .contact-p {
    width: 90%;
    text-align: center;
    font-size: 16px;
    color: white;
  }
}

/* Mobile Screen - 480px and below */
@media (max-width: 480px) {
  .hero {
    height: 300px;
    padding-top: 120px;
  }

  .contact-container {
    padding: 15px;
    flex-direction: column;
    align-items: center;
  }

  .contact-info
  {
    width: 100%;
  }

  .contact-form {
    width: 90%;
    margin: auto;
    padding: 15px;
  }

  .form-group {
    flex-direction: column;
    gap: 10px;
  }

  .form-group input,
  .form-group textarea {
    width: 80%;
    margin: auto;
  }

  .contact-p {
    width: 80%;
    margin: auto;
    padding: 0 10px;
    text-align: center;
    font-size: 14px;
    color: white;
  }

  .info-item {
    flex-direction: row;
    gap: 10px;
    align-items: flex-start;
  }

  .contact-icon {
    font-size: 20px;
  }

  .cont-btn {
    font-size: 14px;
    padding: 10px;
  }
}
