.footer {
  background: #0c2d48;
  color: white;
  border-top: 1px solid gold;
  padding: 40px 20px;
  font-family: Arial, sans-serif;
  margin-top: 20px;
}

.footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1200px;
  margin: auto;
}

.footer-section {
  flex: 1;
  min-width: 250px;
  margin-bottom: 20px;
}

.footer-section h3 {
  font-size: 18px;
  margin-bottom: 15px;
}

.footer-section p {
  font-size: 14px;
  margin-bottom: 10px;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section ul li {
  margin-bottom: 8px;
}

.footer-section ul li a {
  color: white;
  text-decoration: none;
}

.footer-section ul li a:hover {
  text-decoration: underline;
}

.social-icons a {
  color: white;
  font-size: 20px;
  margin-right: 10px;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5px;
}

.gallery img {
  width: 80px;
  height: 60px;
  border-radius: 5px;
}

.newsletter {
  display: flex;
}

.newsletter input {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: 5px 0 0 5px;
}

.newsletter button {
  background: goldenrod;
  color: white;
  border: none;
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 0 5px 5px 0;
  transition: background 0.3s ease;
}

.newsletter button:hover {
  background: gold;
}

.footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  text-align: center;
  padding-top: 10px;
  margin-top: 20px;
}

.footer-bottom a {
  color: goldenrod;
  text-decoration: none;
}

.footer-bottom a:hover {
  text-decoration: underline;
}

.footer-links {
  margin-top: 10px;
}

.footer-links a {
  color: white;
  margin: 0 10px;
  text-decoration: none;
}

.footer-links a:hover {
  text-decoration: underline;
}

.footer-social-bottom {
  margin-top: 10px;
  display: flex;
  gap: 20px;
  justify-content: center;
}

.footer-social-bottom a {
  color: #fff;
  font-size: 35px;
  transition: color 0.3s ease;
}

.footer-social-bottom a:hover {
  color: goldenrod;
}

/* Responsive Design */

/* <= 1024px */
@media (max-width: 1024px) {
  .footer-container {
    justify-content: center;
    gap: 30px;
  }

  .footer-section {
    min-width: 220px;
  }
}

/* <= 768px */
@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }

  .footer-section {
    margin-bottom: 15px;
    max-width: 90%;
  }

  .newsletter {
    flex-direction: column;
    max-width: 90%;
    margin: auto;
  }

  .newsletter input {
    border-radius: 5px;
    margin-bottom: 10px;
    width: 100%;
  }

  .newsletter button {
    border-radius: 5px;
    width: 100%;
  }
}

/* <= 480px */
@media (max-width: 480px) {
  .footer-container {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }

  .footer-section {
    margin-bottom: 20px;
    max-width: 100%;
  }

  .newsletter {
    flex-direction: column;
    max-width: 100%;
    margin: auto;
  }

  .newsletter input {
    border-radius: 5px;
    margin-bottom: 10px;
    width: 100%;
  }

  .newsletter button {
    border-radius: 5px;
    width: 100%;
  }

  .gallery img {
    width: 60px;
    height: 45px;
  }

  .footer-social-bottom a {
    font-size: 28px;
  }
}
