/* Admin Navbar Styling */
.admin-nav {
  width: calc(100% - 250px);
  /* Adjust width based on sidebar */
  height: 60px;
  background-color: #0c2d48;
  color: white;
  position: fixed;
  top: 0;
  left: 250px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  z-index: 1000;
}

.admin-nav-left,
.admin-nav-center,
.admin-nav-right {
  display: flex;
  align-items: center;
}

.admin-logo {
  font-size: 20px;
  font-weight: bold;
}

.admin-icon {
  font-size: 18px;
  margin: 0 10px;
  cursor: pointer;
}

.admin-notification {
  position: relative;
}

.admin-dot {
  position: absolute;
  top: 3px;
  right: 3px;
  width: 8px;
  height: 8px;
  background-color: red;
  border-radius: 50%;
}

.admin-search-box {
  display: flex;
  align-items: center;
  background: white;
  padding: 5px 10px;
  border-radius: 20px;
  margin-left: 10px;
}

.admin-search-box input {
  border: none;
  outline: none;
  background: transparent;
  padding: 5px;
  width: 100px;
}

.admin-search-icon {
  color: black;
}

/* Navbar Responsive Design */
@media screen and (max-width: 768px) {
  .admin-search-box {
    width: 120px;
  }

  .admin-icon {
    margin: 0 5px;
  }
}

@media screen and (max-width: 480px) {
  .admin-nav-center {
    display: none;
  }

  .admin-search-box {
    width: 80px;
  }
}

/* Sidebar Styling */
.admin-sidebar {
  width: 250px;
  height: 100vh;
  background-color: #f8f9fa;
  position: fixed;
  left: 0;
  top: 0;
  overflow-y: auto;
  z-index: 999;
}

/* Profile Section */
.admin-profile {
  text-align: center;
  margin-bottom: 20px;
}

.admin-brand {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  gap: 20px;
}

.admin-home-btn {
  background-color: #0c2d48;
  color: white;
  padding: 5px 15px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  border: none;
  cursor: pointer;
}

.admin-home-btn span {
  margin-left: 5px;
}

.admin-role {
  font-size: 14px;
  color: #6c757d;
  padding-top: 10px;
}

.admin-avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin: 10px 0;
  border: 3px solid white;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.admin-role-text {
  color: #6c757d;
  font-size: 14px;
}

.admin-social-icons {
  display: flex;
  justify-content: center;
  gap: 10px;
  font-size: 18px;
  margin-top: 10px;
}

.admin-social-icons svg {
  cursor: pointer;
  color: #7d6a91;
}

/* Sidebar Menu */
.admin-menu {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 10px 0;
}

.admin-menu-title {
  font-size: 14px;
  color: #6c757d;
  margin: 20px 0 10px 20px;
  text-transform: uppercase;
  font-weight: 600;
}

.admin-menu a {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 16px;
  color: #495057;
  text-decoration: none;
  padding: 10px 20px;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.admin-menu a svg {
  font-size: 20px;
  color: #6c757d;
  transition: color 0.3s ease;
}

.admin-menu a:hover,
.admin-menu a.active {
  background-color: #0c2d48;
  color: #ffffff;
  font-weight: 600;
}

.admin-menu a:hover svg,
.admin-menu a.active svg {
  color: #ffffff;
}

/* Responsive Sidebar */
@media screen and (max-width: 768px) {
  .admin-sidebar {
    width: 200px;
  }

  .admin-menu a {
    font-size: 14px;
    padding: 8px 15px;
  }
}

@media screen and (max-width: 480px) {
  .admin-sidebar {
    width: 80px;
  }

  .admin-menu a {
    justify-content: center;
    padding: 10px;
  }

  .admin-menu a span,
  .admin-menu-title {
    display: none;
  }

  .admin-menu a svg {
    margin: 0;
    font-size: 24px;
  }
}