.hero-container {
  width: 100%;
  height: 400px;
  background-image: url('../Image//about.jpg');
  padding-top: 220px;
  position: relative;
  background-size: cover;
  background-position: center;
}

.hero-overlay {
  max-width: 1000px;
  height: 300px;
  margin: auto;
  background: rgba(0, 0, 0, 0.5);
  padding: 30px;
  text-align: center;
  border-radius: 20px;
  width: 100%;
  color: #fff;
}

.head-title {
  font-size: 2.5rem;
  color: #fff;
}

.trust-badge {
  color: orange;
  font-size: 14px;
  font-weight: bold;
}

.hero-title {
  text-align: center;
  font-size: 36px;
  font-weight: bold;
  max-width: 700px;
  margin: auto;
}

.stats {
  max-width: 650px;
  margin: auto;
  display: flex;
  gap: 20px;
  margin-top: 20px;
  font-size: 14px;
}

.stats-pro {
  padding: 5px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 30px;
  color: #fff;
}

/* HOUSE SERVICE SECTION */

.house-service {
  display: flex;
  align-items: center;
  gap: 30px;
  padding: 20px;
  max-width: 90%;
  margin: 10px auto;
  background-color: #f9f9f9;
}

.house-image img {
  width: 400px;
  height: 400px;
  border-radius: 10px;
  object-fit: cover;
  margin-top: 0;
}

.house-content {
  flex: 1;
}

.house-content h2 {
  font-size: 2.5rem;
  color: #e69500;
  margin-bottom: 10px;
  text-align: left;
}

.house-content-p {
  font-size: 16px;
  line-height: 1.5;
  width: 800px;
}

.highlight {
  font-size: 18px;
  color: #000;
  font-weight: bold;
}

.house-buttons {
  margin-top: 20px;
  display: flex;
  gap: 15px;
}

.btn {
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  text-decoration: none;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s ease;
  color: white;
}

.call-btn {
  background-color: #007bff;
}

.call-btn:hover {
  background-color: #0056b3;
}

.whatsapp-btn {
  background-color: #25d366;
}

.whatsapp-btn:hover {
  background-color: #1ebe5d;
}

/* TABLET SCREEN */
@media (max-width: 768px) {
  .hero-container {
    width: 100%;
    height: 450px;
  }

  .hero-overlay {
    width: 80%;
    height: 350px;
  }

  .hero-title {
    font-size: 24px;
  }

  .stats {
    flex-direction: column;
    text-align: center;
  }

  .house-service {
    flex-direction: column;
    text-align: center;
  }

  .house-image img {
    width: 100%;
    max-width: 100%;
  }

  .house-content {
    width: 100%;
  }

  .house-content-p {
    text-align: left;
    width: 100%;
  }

  .house-content h2,
  .house-content h3 {
    font-size: 20px;
    text-align: left;
  }
}

/* MOBILE SCREEN */
@media (max-width: 480px) {
  .hero-container {
    height: 480px;
    padding-top: 180px;
  }

  .hero-overlay {
    width: 90%;
    height: auto;
    padding: 20px;
  }

  .hero-title {
    font-size: 20px;
  }

  .head-title {
    font-size: 1.8rem;
  }

  .stats {
    flex-direction: column;
    font-size: 12px;
    gap: 10px;
  }

  .house-service {
    flex-direction: column;
    text-align: center;
    padding: 15px;
  }

  .house-image img {
    width: 100%;
    height: auto;
    max-width: 100%;
    border-radius: 10px;
  }

  .house-content {
    width: 100%;
    padding: 0 10px;
  }

  .house-content h2,
  .house-content h3 {
    font-size: 20px;
    text-align: center;
  }

  .house-content-p {
    font-size: 14px;
    line-height: 1.6;
    width: 100%;
    text-align: justify;
  }

  .house-buttons {
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-top: 15px;
  }

  .btn {
    width: 100%;
    max-width: 250px;
    font-size: 14px;
  }
}
