/* SliderForm.css */

.slider-container {
    max-width: 600px;
    margin: 120px auto 20px auto;
    /* Adjusted margin-top */
    background: #ffffff;
    border: 1px solid #ddd;
    border-radius: 8px;
}

.slider-heading {
    text-align: center;
    margin-bottom: 20px;
}

.slider-form {
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 20px;
    /* Added padding to the form */
}

.slider-form label {
    font-weight: bold;
    margin-bottom: 5px;
    text-align: left;
    /* Align labels to the left */
}

.slider-form input[type="text"],
.slider-form textarea,
.slider-form input[type="file"] {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%;
    /* Make input fields take full width of the form */
    margin: 0 auto;
    /* Remove auto margin for full width */
    box-sizing: border-box;
    /* Include padding and border in element's total width and height */
}

.slider-form textarea {
    resize: vertical;
    min-height: 80px;
}

.slider-form button {
    padding: 15px;
    background-color: #007bff;
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    width: 100%;
    /* Make button take full width of the form */
    margin: 10px auto 0 auto;
    /* Adjust button margin */
    box-sizing: border-box;
}

.slider-form button:hover {
    background-color: #0056b3;
}

.slider-preview {
    margin-top: 30px;
    padding: 20px;
    /* Added padding to the preview section */
}

.slider-preview h3 {
    margin-bottom: 15px;
    text-align: center;
    /* Center the preview heading */
}

.preview-cards-container {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
}

.preview-card {
    flex: 1 1 100%;
    border: 1px solid #ccc;
    padding: 12px;
    border-radius: 6px;
    background-color: #f9f9f9;
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
}

.preview-card .card-image {
    width: 100%;
    margin-bottom: 10px;
    border-radius: 6px;
    overflow: hidden;
    /* Clip image to border-radius */
}

.preview-card .card-image img {
    display: block;
    width: 100%;
    height: auto;
    object-fit: cover;
    aspect-ratio: 16 / 9;
    /* Optional: Maintain a 16:9 aspect ratio */
}

.preview-card .card-content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    /* Allow content to take up remaining vertical space */
}

.preview-card .card-content h4 {
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 1.2rem;
    font-weight: bold;
}

.preview-card .card-content p {
    margin-bottom: 10px;
    font-size: 0.9rem;
    color: #555;
    flex-grow: 1;
    /* Allow description to grow */
}

.preview-card .card-content .delete-button {
    background-color: #f44336;
    color: white;
    border: none;
    padding: 8px 15px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.9rem;
    align-self: flex-start;
    /* Align button to the left */
    width: auto;
    /* Adjust width to content */
}

.preview-card .card-content .delete-button:hover {
    background-color: #d32f2f;
}

/* Media Query for larger screens */
@media screen and (min-width: 600px) {
    .preview-card {
        flex: 1 1 calc(50% - 16px);
    }
}

@media screen and (min-width: 900px) {
    .preview-card {
        flex: 1 1 calc(33.33% - 16px);
    }

    .slider-container {
        max-width: 800px;
    } }